.point {
  font-size: 11px;
  text-align: left;
  width: 45%;
  padding: 5px;
  /*border: 1px black solid;*/
  margin: -10px;
  border-left: 4px black solid;
}

.info {
  margin-left: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
  width: 45%;
}

.name {
  padding-bottom: 10px;
}

.info:hover {
  background-color: rgba(0,0,0, 0.8);
  color: white;
  -webkit-transition: background-color 1000ms linear;
  -o-transition: background-color 1000ms linear;
  transition: background-color 1000ms linear;
}

.content {
  display: inline;
}

.hover-content {
  display: none;
}

.info:hover .content {
  display:none;
}

.info:hover .hover-content {
  display: inline;
}


